//----------  variables
//------------------------------------------------------------------------------

$font-default: 'Open Sans', sans-serif;
$font-ralaway: 'Raleway', sans-serif;

// Colors
$white: #fff;
$black: #000;
$orange: #fbb040;
$grey-dark: #231f20;
$grey-light: #bcbec0;
$grey: #e2e2e2;

$site-colors: (
	'black': $black,
	'white': $white,
	'orange': $orange
) !default;

//----------  Bootstrap grid
//------------------------------------------------------------------------------

$enable-grid-classes:       true !default;

//----------  Media queries breakpoints - Grid breakpoint
//------------------------------------------------------------------------------

$grid-breakpoints: (
  xs: 0,
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px
) !default;

//----------  Grid containers
//------------------------------------------------------------------------------

$container-max-widths: (
  sm: 540px,
  md: 720px,
  lg: 960px,
  xl: 1140px
) !default;

//----------  Grid columns
//------------------------------------------------------------------------------

$grid-columns: 12 !default;
$grid-gutter-width: 30px !default;
